import React from "react"
import { Link } from "gatsby"
import Header from "src/components/Header"
import { AboutUs, ContactUsButton } from "./About.styled"
import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome"
// import { faTimes, faAngleDown } from "@fortawesome/pro-light-svg-icons"
import { faWindowMaximize as faPhoneAlt } from "@fortawesome/pro-solid-svg-icons"
import ImgCarousel from "src/components/Carousels/AboutUs"
import ContactPopup from "./ContactPopup"
import { _, __ } from "src/containers/Domains/Domains.styled"

class AboutPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      popupActive: false
    }
  }
  componentDidMount = async () => {
    // open popup after viewer sees the aboutus content
    if (this.props.open_contact) {
      setTimeout(() => {
        this.setState({ popupActive: true })
      }, 500)
    }
  }
  componentDidUpdate = async (prevProps) => {
    // open popup immediately
    if (this.props.open_contact && this.props.open_contact !== prevProps.open_contact) {
      this.setState({ popupActive: true })
    }
  }
  render() {
    return (
      <>
        <Header location={this.props.location} standalone={true} />

        {/*
         * Page content
         */}
        <AboutUs className="content">
          <div className="before">
            <div className="titleWithButton">
              <h2>About us:</h2>
              <ContactUsButton
                className="ContactUsButton clickable"
                onClick={() => {
                  this.setState({ popupActive: true })
                }}
              >
                Contact us&thinsp;
                <FA icon={faPhoneAlt} className="" />
              </ContactUsButton>
            </div>
            <p>
              A husband and wife team, based in Kansas City, MO (USA). üë®üèº‚Äçüíªüë©üèΩ‚Äçüíº Combining our talents, we were able to make
              the world's best English language <Link to="/word?str=best">thesaurus</Link>. Paul built custom admin
              tools to automate content editing. Samira used them to edit countless words and results. We plan to build
              on this foundation and expand our domain and text analysis APIs, then expand to more content-rich
              applications.
            </p>
            <ImgCarousel />
          </div>
          <p className="bio">
            <span className="color-attention">
              <b>Paul Shorey</b> has worked 11 years with web development and software engineering teams in NYC, Utah,
              California, and remotely.
            </span>{" "}
            {/*Finally getting pretty good at coding. */}
            He specializes in front-end and back-end Javascript, microservices/API development, and UI/UX design.
            Self-taught, Paul actually graduated from art school. There is a fundamental similarity between fine-art and
            software engineering - both let you create something from nothing.{" "}
            {!this.state.showPaul ? (
              <span
                className="link"
                onClick={() => {
                  this.setState({ showPaul: true })
                }}
              >
                ...
              </span>
            ) : (
              <span>
                Paul was born in Russia, but is now a US citizen. He has lived all around this beautiful country. His
                favorite states are Colorado and Utah. His favorite hobbies are hang gliding and skiing, though for now,
                he's focusing on not breaking an arm, but getting this AI business off the ground.
              </span>
            )}{" "}
            &nbsp;
            <a className="link" href="https://paulshorey.com" target="_blank">
              PaulShorey.com
            </a>
            {/*<span className="readMore">*/}
            {/*  read more <FA icon={faAngleDown} className="" />*/}
            {/*</span>*/}
          </p>
          <p className="bio">
            <span className="color-attention">
              <b>Samira Ali</b> has a degree in Sociology.
              {/*, and is talented in linguistics and design.*/}
            </span>{" "}
            She speaks Swahili and Mandarin, can read/write Arabic, and is learning Spanish. Samira has worked as a
            social media coordinator, graphic designer, researcher, and an electronic health records consultant. She has
            been a part of several non-profits around Kansas City and has started her own initiatives. Samira just wants
            to help people.{" "}
            {!this.state.showSamira ? (
              <span
                className="link"
                onClick={() => {
                  this.setState({ showSamira: true })
                }}
              >
                ...
              </span>
            ) : (
              <span>
                She has been accepted into medical school, but that is now deferred for immigration reasons. So, she's
                currently working full time on this AI venture. Samira is considering staying in tech, and going to
                graduate school for Linguistics instead.
              </span>
            )}
            {/*Samira just wants to help people... */}
            {/*<span className="readMore">*/}
            {/*  read more <FA icon={faAngleDown} className="" />*/}
            {/*</span>*/}
          </p>
          {/*<p className="bio">More info coming soon...</p>*/}
          <p className="bio">
            <span
              className="link"
              onClick={() => {
                this.setState({ popupActive: true })
              }}
            >
              Lets meet...
            </span>
          </p>
          <div className="before">
            <div className="titleWithButton">
              <h2>This is a work in progress...</h2>
            </div>
            <p>
              Currently finishing the domain suggestions API for registrars, and app for consumers -{" "}
              <a className="link" href="https://nlpthesaurus.com" target="_blank">
                NLP.Domains
              </a>
              . Also launching the world's best English language thesaurus for AI and text analysis applications -{" "}
              <a className="link" href="https://nlpthesaurus.com" target="_blank">
                NLP
                <_ />
                Thesaurus
                <_ />
                .com
              </a>
            </p>
            <p>
              <b>Coming soon: </b> more nifty domain tools, text analysis and sentiment analysis APIs, then we'll test
              these tools on more demanding text-based data science projects: aggregation, categorization,
              visualization, and search of publicly available online content (ex: indie movie recommendations,
              e-business resources, calendar of online gatherings, map of cultural and social places, and dashboards to
              monitor economic or political activity) -{" "}
              <a className="link" href="https://nlp.studio" target="_blank">
                NLP.Studio
              </a>
            </p>
          </div>
          <div className="after">
            <p className="bio">
              <b>This is not a get rich quick scheme </b> for us but a journey. We're not looking for quick investment
              money, but in building a repository of useful tools. We hope to meet partners, mentors, and friends. Thank
              you for your visit today. Thank you for your support.
              {/*- a repository for tools and knowledge - an opportunity to meet partners and mentors*/}
              {/*A collection of tools and resources, a foot in the door to meet partners and mentors.*/}
            </p>
            <p className="bio">
              <b>Please do contact us, </b>for any reason. What is your first impression of the site, of the
              suggestions, the thesaurus? Please do try it, a few times. Please let us know if one of the search terms
              did not give good results. We're still working on it.
            </p>
            <p className="bio">
              <b>Lets work together! </b>Let's build something together. After NLP.Domains and NLPThesaurus.com, our
              plans are to just gather experience and partners, while building cool things. If you are building
              something interesting, we'd love to be a part of it! We'd love to contribute our experience to your team.
            </p>
            {/*<p className="bio">*/}
            {/*  <b>Please join our mailing list,</b> for very in-frequent emails - to be the first to know when we launch*/}
            {/*  a new product or major feature.*/}
            {/*</p>*/}
          </div>
          <div className="after">
            <ContactUsButton
              className="ContactUsButton clickable"
              onClick={() => {
                this.setState({ popupActive: true })
              }}
            >
              Contact us&thinsp;
              <FA icon={faPhoneAlt} className="" />
            </ContactUsButton>
          </div>
          <p>&nbsp;</p>
          <p>&nbsp;</p>
          <p>&nbsp;</p>
        </AboutUs>
        {/*
         * Contact popup
         */}
        <ContactPopup
          show={!!this.state.popupActive}
          onClose={() => {
            this.setState({ popupActive: false })
          }}
        />
      </>
    )
  }
}
export default AboutPage

